[role=tab] {
  position: relative;
  z-index: 2;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  column-gap: 20px;
  padding-top: 24px;
  padding-bottom: 24px;
  padding-left: 16px;
  padding-right: 16px;
  outline: none;
  font-weight: 700;
  font-size: 0.75rem;
  line-height: 1;
  text-transform: uppercase;
  color: var(--dark-gray);
  border-left: 1px solid var(--mid-grey);
  border-top: 1px solid var(--mid-grey);
  border-bottom: 1px solid var(--mid-grey);
  background: var(--light-gray);
  white-space: nowrap;
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}
[role=tab]:last-child {
  border-right: 1px solid var(--mid-grey);
}
[role=tab] svg {
  fill: var(--dark-gray);
}

[role=tab]:focus,
[role=tab]:hover {
  color: var(--green-text);
  background-color: var(--white);
}
[role=tab]:focus svg,
[role=tab]:hover svg {
  fill: var(--green-text);
}

[role=tab][aria-selected=true] {
  color: var(--white-text);
  background-color: var(--green);
  border-color: var(--green-text);
}
[role=tab][aria-selected=true] svg {
  fill: var(--whiteText);
}

[role=tabpanel] {
  position: relative;
}
[role=tabpanel]:before {
  content: "";
  position: absolute;
  bottom: -16px;
  left: -32px;
  display: block;
  height: 1px;
  width: calc(100% + 64px);
  background-color: var(--mid-grey);
}
[role=tabpanel]:last-child::after {
  content: "";
  position: absolute;
  top: -16px;
  right: -16px;
  display: block;
  width: 1px;
  background-color: var(--mid-grey);
  height: calc(100% + 32px);
}
[role=tabpanel]:last-child:before {
  width: calc(100% + 48px);
}
[role=tabpanel]:first-child:after {
  content: "";
  position: absolute;
  top: -16px;
  left: -16px;
  display: block;
  width: 1px;
  background-color: var(--mid-grey);
  height: calc(100% + 32px);
}
[role=tabpanel]:first-child:before {
  left: -16px;
  width: calc(100% + 48px);
}

[role=tabpanel].is-hidden {
  display: none;
}

@media (min-width: 740px) {
  [role=tab] {
    padding-top: 30px;
    padding-bottom: 31px;
    padding-left: 40px;
    padding-right: 40px;
  }

  .tabs-horizon__panels {
    padding-top: 36px;
  }

  [role=tabpanel]:last-child::after {
    top: -36px;
    height: calc(100% + 52px);
  }
  [role=tabpanel]:first-child::after {
    top: -36px;
    height: calc(100% + 52px);
  }
}
