    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
  }
  [role=tabpanel]::before {
    display: none;
  }
  [role=tabpanel]:last-child::after {
    display: none;
  }
  [role=tabpanel]:first-child:after {
    display: none;
  }

  [role=tab] {
    background-color: var(--white);
  }
}
